import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import About from '@/views/About'
import SignIn from '@/components/SignIn'
import Signup from '@/components/Signup'
import PrivateChat from '@/components/PrivateChat'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/sign-in',
      name: 'SignIn',
      component: SignIn
    },
    {
      path: '*',
      redirect: '/sign-in',
    },
    {
      path: '/sign-up',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/privatechat',
      name: 'PrivateChat',
      component: PrivateChat,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/about',
      name: 'About',
      component: About
    }
  ]
});

router.beforeEach((to,from,next)=>{
  let currentUser=firebase.auth().currentUser;
  //alert(JSON.stringify(currentUser));
  let requiresAuth=to.matched.some(record=>record.meta.requiresAuth);

  if(!currentUser){
    if(requiresAuth && !currentUser) next('sign-in')
    else if (!requiresAuth && currentUser) next('privatechat')
    else next()
  }else if(currentUser.uid.length>1){
    //alert(JSON.stringify(currentUser));
    //if(requiresAuth && (currentUser.uid.length<1)) next('sign-in')
    if (!requiresAuth && (currentUser.uid.length>1)) next('hello')
    else next()
  }
  //let currentUser=firebase.auth().currentUser;

  //if(!currentUser) next('sign-in')
  //else next()
});

export default router



