<template>
  <div id="app">
    <div class="container">
    <div class="navbar">
      <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <a class="navbar-brand" href="#">VueFirebaseChat</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav mr-auto">
            <li class="nav-item active">
              <a class="nav-link" href="#" v-if="!authUser"><router-link to="/sign-in">SignIn</router-link> <span class="sr-only">(current)</span></a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" v-if="!authUser"><router-link to="/sign-up">SignUp</router-link></a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" v-if="!authUser"><router-link to="/about">About</router-link></a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" v-if="authUser"><router-link to="/privatechat">Private Chat</router-link></a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" v-if="authUser"><router-link to="/home">Movies</router-link></a>
            </li>
            <li class="nav-item"  v-if="authUser" style="margin-top:8px;">
                <a href="#" v-on:click="logout"> Logout</a>
                <a href="#">{{authUser.identifier}}</a>
            </li>
          </ul>
        </div>
      </nav>
    </div>
    </div>

    <img src="https://vasileclaudiu.github.io/vuefirebasechat/img/logo.png" width="10%" height="10%">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      authUser:null
    }
  },
  watch:{
    '$route':'setAuthUser'
  },

  methods:{
    setAuthUser(){
      this.authUser=firebase.auth().currentUser;
    },
    logout(){
      firebase.auth().signOut()
        .then(()=>{
          this.$router.replace('/sign-in')
        })
        .catch((e)=>{
          alert(e.message)
        })
    }
  },
  created(){
    this.setAuthUser();
    // this.authUser=firebase.auth().currentUser;
  }
}
</script>
