<template>
    <div class="container">
        <h2>Signup</h2>
        <div class="row">
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 mx-auto">
            <div class="group-input form-inline">
            <label for="name">Name</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="text" name="name" v-model="formData.name" placeholder="Name"/>
            </div>
            <div class="group-input form-inline">
            <label for="nickname">Nickname</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="text" nickname="nickname" v-model="formData.nickname" placeholder="Nickname"/>
            </div>
            <div class="group-input form-inline">
            <label for="email">Email</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="email" name="email" v-model="formData.email" placeholder="email"/>
            </div>
            <div class="group-input form-inline">
            <label for="password">Password</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="password" name="password" v-model="formData.password" placeholder="password"/>
            </div>
            <button class="btn btn-success" @click="signUp" style="margin-top: 20px;">SignUp</button>
        </div>
        </div>
    </div>
</template>
<script>
    import moment from 'moment';

    export default {
        name: 'Signup',
        data () {
            return {
                formData:{
                    name:'',
                    nickname:'',
                    email:'',
                    password:'',
                }
            }
        },
        methods: {
            signUp(){
                firebase.auth().createUserWithEmailAndPassword(this.formData.email,this.formData.password)
                    .then((user)=>{
                      this.$router.replace('/hello')
                    })
                    .catch((e)=>{
                    alert('oops'+e.message);
                    })
                var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
                firebase.database().ref('users').push({
                    name:this.formData.name,
                    nickname: this.formData.nickname,
                    email: this.formData.email,
                    password: this.formData.password,
                    time: curtime
                });
                
            },
            fetchFirebaseData(){
            firebase.database().ref('users').on('value',(snapshot)=>{
                this.fireData=snapshot.val();
            });
            }
        },

        created(){
            this.fetchFirebaseData();
        }

    }
</script>

