<template>
    <div class="container">
        <h2>Signin</h2>

        <div class="row">
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 mx-auto">
            <div class="group-input form-inline">
            <label for="email">Email</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="email" name="email" v-model="formData.email" placeholder="email" />
            </div>
            <div class="group-input form-inline">
            <label for="password">Password</label>
            <input class="col-xs-9 col-sm-9 col-md-9 col-lg-9 form-control ml-auto" type="password" v-model="formData.password" placeholder="password"  @keyup.enter="signIn" />
            </div>
            <button class="btn btn-success" @click="signIn" style="margin-top: 20px;">Signin</button>
        </div>
        </div>

    </div>
</template>

<script>

    export default {
        name: 'SignIn',
        data () {
            return {
                formData:{
                    email:'',
                    password:''
                }
            }
        },
        methods: {
            signIn(){
                firebase.auth().signInWithEmailAndPassword(this.formData.email,this.formData.password)
                    .then((user)=>{
                      this.$router.replace('/privatechat')

                    })
                    .catch((e)=>{
                        alert(e.message)
                    })
            }
        },

        created(){

        }

    }
</script>

